.cards {
  > button {
    @media print {
      display: none;
    }
  }

  > div {
    display: grid;
    grid-auto-columns: 63mm;
    grid-auto-flow: row dense;
    grid-auto-rows: calc(#{5.5in - 44mm - 1px}) 88mm calc(#{5.5in - 44mm - 1px});
    grid-template-areas: '. . .';
    margin: 0 auto;
    width: fit-content;

    @media print {
      grid-gap: 0;
    }

    @media screen {
      grid-gap: 1rem;
    }
  }
}
